import React from "react";
import style from "./style.module.css";

export default function Button(props) {
  const { children, type } = props;

  return (
    <button
      {...props}
      className={`${style.button} ${
        type == "primary" ? style.primary : style.default
      }`}
    >
      {children}
    </button>
  );
}
